body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	background: #FFF;
	color:#333;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,p{
  padding:0 10px;
}

h1{
  font-size:1em;
  margin:0;
  line-height: 50px;
  background: #000;
  color: #FFF;
}

.goo {
  background-color: white;
  overflow:hidden;
  filter: brightness(1.4) contrast(40);
}
.circle {
  border-radius: 50%;
  background-color: red;
  filter: blur(15px);
  width: 1px;
  height: 1px;
}
.circle:first-child {
  margin-bottom: -10px;
}
.circle:nth-child(3) {
  margin-left: 15px;
}
svg{
	background: #212526;
	margin:2px;
}
.glanceable svg{
	border:20px solid #212526;
}
#debug{
	opacity:0.1;
	transition: all 200ms;
}
svg:hover #debug{
	opacity:1;
}
svg #debug circle{
	fill:red;
}
svg #debug line{
	stroke:red;
}
.svg{
  display: inline-block;
	position: relative;
  margin:5px;
  width:100px;
  height: 100px;
}
.selected svg{
  outline:2px solid #67da36;
}
.svg:after{
	display: none;
	width: calc(100% - 10px);
	height:100%;
	background: rgba(255,255,255,0.1);
	content:attr(data-selected);
	position: absolute;
	left: 2px;
	top: 2px;
	text-align: right;
  padding-right:10px;
	line-height: 25px;
	font-size: 1em;
  color: #DDD;
}
.svg.selected:after{
	display: block;
}
.example, .murvs{
  clear:both;
  text-align: center;
}
.example{
  border-bottom: 1px solid #CCC;
  padding-bottom: 10px;
  margin-bottom: 10px;
}
button{
  font: inherit;
  background: #212526;
  border: 0;
  color: #DDD;
  padding: 10px 20px;
  border-left: 3px solid #DDD;
  cursor: pointer;
  float: right;
  margin: 20px 0 20px 10px;
}
.content{
  width:970px;
  margin:0 auto;
}
.App{
  text-align: center;
}
textarea{
  width:calc(100% - 22px);
  border:1px solid #DDD;
  padding:10px;
  line-height: 30px;
  height:90px;
  font:inherit;
}
#bangorLogo {
    height: 70px;
    background: #FFF;
    clear: both;
    border: 20px solid #FFF;
    border-right: 600px solid #FFF;
    opacity: 0.8;
    position: absolute;
    bottom: 20px;
    right: -4px;
    box-shadow: 1px 1px 1px #000;
}
